import React, { Component } from 'react';
import { Input, Button, Upload, Form, Spin } from 'antd';

import 'antd/dist/antd.css';

const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 8 },
};
const formTailLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 8, offset: 4 },
};

class DropboxForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fetching: false,
            clientName: "",
            files: [],
            dropboxPath: "",
            mondayGroupId: "",
            mondayItemId: ""
        }

        this.handleClientNameChange = this.handleClientNameChange.bind(this);
        this.handleFileChange = this.handleFileChange.bind(this);
        this.handleFormSubmission = this.handleFormSubmission.bind(this);
    }

    handleClientNameChange(event) {
        this.setState({ clientName: event.target.value });
    }

    handleFileChange({ file, onSuccess }) {
        let updatedFiles = this.state.files;
        updatedFiles.push(file);
        return this.setState({ files: updatedFiles }, () => {
                setTimeout(() => {
                    onSuccess("ok");
                }, 0);
            })
    }

    createClientFolder() {
        return fetch('https://api.dropboxapi.com/2/files/create_folder_v2', {
            method: 'POST',
            mode: "cors",
            headers: {
                "Authorization": `Bearer N53yk6zNCx8AAAAAAAAAAX1hzyvOR2HvK_h3ZD-6on76RGEi1spj2ZyD5gjgmRFt`,
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                "path": `/Volt Onboarding/${this.state.clientName}`
            })
        })
        .then(res => res.json())
        .then(data => {
            const dropboxPath = "https://www.dropbox.com/home/justin%20bavier"+data.metadata.path_display.replaceAll(" ", "%20")
            this.setState({ dropboxPath: dropboxPath })
        })
    }

    uploadClientFile() {  
        return Promise.all(this.state.files.map((file, i) => {
            return new Promise(resolve => 
                setTimeout(() => {
                    const formData = new FormData();
                    formData.append('File', file);
                    fetch("https://content.dropboxapi.com/2/files/upload", {
                        method: 'POST',
                        mode: 'cors',
                        headers: {
                            "Authorization": `Bearer N53yk6zNCx8AAAAAAAAAAX1hzyvOR2HvK_h3ZD-6on76RGEi1spj2ZyD5gjgmRFt`,
                            "Content-Type": "application/octet-stream",
                            "Dropbox-API-Arg": JSON.stringify({
                                "path":   `/Volt Onboarding/${this.state.clientName}/${file.name}`,
                                "mode": "add",
                                "autorename": true,
                                "mute": false,
                                "strict_conflict": false
                            })
                        },
                        body: this.state.files[i]
                    })     
                    resolve()                        
                }, 2000 * this.state.files.length - 2000 * i)          
            )
        })).then(function() {
            console.log("Client file's uploaded.");
        })
    }

    createMondayGroup() {
        let queryGroup = `mutation{ create_group (board_id:1435220377, group_name:\"${this.state.clientName}\") { id } }`
        return fetch("https://api.monday.com/v2", {
            method: 'POST',
            mode: 'cors',
            headers: {
                "Authorization": `eyJhbGciOiJIUzI1NiJ9.eyJ0aWQiOjExMTczMTE0OCwidWlkIjoxMzAzNzE2MCwiaWFkIjoiMjAyMS0wNS0yOFQxNjowNjo1Mi42MjRaIiwicGVyIjoibWU6d3JpdGUiLCJhY3RpZCI6NTgyODk5MCwicmduIjoidXNlMSJ9.FTuSMwwOCpl8kpTun9U4GM6J9cDuN_XWtWARCQXArhI`,
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
               "query": queryGroup
            })
        })
        .then(res => res.json())
        .then(data => {
            let info = data;
            this.setState({ mondayGroupId: info.data.create_group.id });
        })
    }

    createMondayItem() {
        let queryItem = `mutation{ create_item (board_id: 1435220377, group_id: "${this.state.mondayGroupId}", item_name: "Dropbox Link" ) { id } }`
        return fetch("https://api.monday.com/v2", {
            method: 'POST',
            mode: 'cors',
            headers: {
                "Authorization": `eyJhbGciOiJIUzI1NiJ9.eyJ0aWQiOjExMTczMTE0OCwidWlkIjoxMzAzNzE2MCwiaWFkIjoiMjAyMS0wNS0yOFQxNjowNjo1Mi42MjRaIiwicGVyIjoibWU6d3JpdGUiLCJhY3RpZCI6NTgyODk5MCwicmduIjoidXNlMSJ9.FTuSMwwOCpl8kpTun9U4GM6J9cDuN_XWtWARCQXArhI`,
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
               "query": queryItem
            })
        })
        .then(res => res.json())
        .then(data => {
            let info = data;
            this.setState({ mondayItemId: info.data.create_item.id });
        })
    }

    updateMondayItem() {
        let queryItem = `mutation{ create_update (item_id: ${this.state.mondayItemId}, body: "${this.state.dropboxPath}" ) { id } }`
        return fetch("https://api.monday.com/v2", {
            method: 'POST',
            mode: 'cors',
            headers: {
                "Authorization": `eyJhbGciOiJIUzI1NiJ9.eyJ0aWQiOjExMTczMTE0OCwidWlkIjoxMzAzNzE2MCwiaWFkIjoiMjAyMS0wNS0yOFQxNjowNjo1Mi42MjRaIiwicGVyIjoibWU6d3JpdGUiLCJhY3RpZCI6NTgyODk5MCwicmduIjoidXNlMSJ9.FTuSMwwOCpl8kpTun9U4GM6J9cDuN_XWtWARCQXArhI`,
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
               "query": queryItem
            })
        })
        .then(res => res.json())
        .then(data => {
            let info = data;
            console.log(this.state.dropboxPath)
        })
    }

    sendSlackMessage() {
        return fetch('https://maple-goose-52684.herokuapp.com/send-message', {
            method: 'POST',
            mode: 'no-cors',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "channel": "#development",
                "text": `New Project Inquiry from: ${this.state.clientName} \n
                Dropbox Link: ${this.state.dropboxPath}`
            })
        })
    }

    handleFormSubmission() {
        this.setState({ fetching: true }, () => {
            this.createClientFolder()
                .then(() => { 
                    this.uploadClientFile()
                        .then(() => {
                            this.createMondayGroup()
                                .then(() => {
                                    this.createMondayItem()
                                        .then(() => {
                                            this.updateMondayItem()
                                                .then(() => {
                                                    this.sendSlackMessage()
                                                        .then(() => {
                                                            this.setState({ fetching: false }, () => console.log(this.state.fetching))                                                            
                                                        })
                                                })
                                        })
                                })
                        })
                })
        })
    }

    render() {
        return (<div className="dropbox_container">
        <Form {...formItemLayout}>
            <Form.Item label="Client Name:">
                <Input type="text" value={this.state.clientName} onChange={this.handleClientNameChange} />
            </Form.Item>
            <Form.Item label="Files:" >
                <Upload value={this.state.file} beforeUpload={false} customRequest={this.handleFileChange} multiple={true}>
                    <Button>Click to upload</Button>
                </Upload>
            </Form.Item>
            <Form.Item {...formTailLayout}>
                <Button onClick={() => this.handleFormSubmission()}>Submit</Button>
                <Spin style={{ marginLeft: `15px` }} spinning={this.state.fetching} />
            </Form.Item>
        </Form>
        </div>)
    }
}

export default DropboxForm;