import React, { Component } from 'react';
import { Layout } from 'antd';
import Logo from '../../assets/images/main_logo_white.png';

// Import Components
import DropboxForm from '../components/dropboxForm';
import Navbar from '../components/navigation/navbar';
import FooterContent from '../components/navigation/footerContent';

const { Header, Footer, Sider, Content } = Layout;
  
 


class Index extends Component {
  render() {
    return (<Layout>
      <Sider className="site_sider">
        <div className="site_sider_image_wrapper">
          <img src={Logo} width="150" className="site_logo" />
        </div>
      </Sider>
      <Layout>
          <Header className="site_header">
            <Navbar />
          </Header>
          <Content style={{ padding: '50px 50px' }}>
          <div className="site_layout_content">
            <h1 className="site_page_heading">Client Onboarding Form</h1>
            <DropboxForm />
          </div>
          </Content>
          <Footer className="site_footer">
            <FooterContent />
          </Footer>
      </Layout>
  </Layout>);
  }
}

export default Index;
